import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { MyAppointmentsComponent } from './components/my-appointments/my-appointments.component';
import { BookAppointmentComponent } from './components/book-appointment/book-appointment.component';
import { AppointmentConfirmationComponent } from './components/appointment-confirmation/appointment-confirmation.component';

const routes: Routes = [
  { path: 'my-appointments', component: MyAppointmentsComponent },
  { path: 'book-appointment', component: BookAppointmentComponent },
  { path: 'appointment-confirmation', component: AppointmentConfirmationComponent },
  { path: '', redirectTo: 'my-appointments', pathMatch: 'full' },
  { path: '**', redirectTo: 'my-appointments' }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class AppointmentsRoutingModule { }
